.slider-controls {
  &__control {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;

    top: 50%;

    width: 24px;
    height: 24px;
    margin-top: calc(0px - (24px / 2));

    cursor: pointer;
    user-select: none;

    @include vp-389 {
      width: 16px;
      height: 16px;
    }

    svg {
      flex-shrink: 0;

      width: 24px;
      height: 24px;

      color: $color-nero;

      transition: color $trans-default;

      @include vp-389 {
        width: 16px;
        height: 16px;
      }
    }

    @include hover-focus {
      svg {
        color: $color-pumpkin;
      }
    }

    &--prev {
      left: -27px;


      @include vp-389 {
        left: -20px;
      }
    }

    &--next {
      right: -27px;

      @include vp-389 {
        right: -20px;
      }
    }

    &.swiper-button-disabled {
      pointer-events: none;

      svg {
        color: $color-grey;
      }
    }
  }
}
