.custom-checkbox label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}
.custom-checkbox__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  border-radius: 2px;
  background-color: #ffffff;
  transition: background-color 0.3s ease;
}
.custom-checkbox__icon img {
  flex-shrink: 0;
  width: 14px;
  height: 14px;
  transition: visibility 0.3s ease, opacity 0.3s ease;
  visibility: hidden;
  opacity: 0;
}
.custom-checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-checkbox input:checked + .custom-checkbox__icon img {
  visibility: visible;
  opacity: 1;
}
.custom-checkbox input:checked ~ .custom-checkbox__text {
  opacity: 1;
  visibility: visible;
}
.custom-checkbox__label {
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  user-select: none;
}
